	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.tagesschau.de/meldungen/image_popup/0,1189,OID2381222_IMG2380892_HID2380892_POS0_MTB1,00.html\"><img alt=\"Graph: Union 50% SPD 27%\" title=\"Union 50% SPD 27%\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/10/un50spd27.jpg\" width=\"200\" height=\"150\" border=\"0\" align=\"right\" /></a>Now opinion polls are just polls and not elections, and elections are 3 years away, but the poll ratings for Chancellor Schröder and the SPD continue to hit new lows. The <a title=\"tagesschau.de : SPD verliert weiter an Zustimmung\" href=\"http://www.tagesschau.de/aktuell/meldungen/0,1185,OID2381222_TYP6_THE_NAV_REF1_BAB,00.html\">latest numbers</a> last night on ARD were quite dramatic, reflecting the internal problems Schröder has been having with his left wing and the crest of popularity for Edmund Stoiber and the Union after the Bavarian election. The lowlights for Schröder:</p>\n<ul style=\"list-style: disc inside;\">\n<li>in the 'Sunday question', the Union would get 50%, the SPD 27% (Greens 11%, FDP 5%, PDS 3%)</li>\n<li>for the first time, Schröder would lose a head to head match, against both Merkel and Stoiber</li>\n</ul>\n<p>Message to Schröder: 'we don't like what you're doing, and it's your fault'. A few weks ago, I didn't believe the ruling coalition was in any serious mid-term danger. Now I do.</p>","fields":{"title":"It's your fault","date":"04 Oct 2003","url":"https://papascott.de/2003-10-04-its-your-fault/"}}},"pathContext":{"slug":"/2003-10-04-its-your-fault/","prev":{"fields":{"title":"AL Playoffs: Yankees 3 Twins 1","date":"04 Oct 2003","slug":"/2003-10-04-al-playoffs-yankees-3-twins-1/"}},"next":{"fields":{"title":"Animal Park Overload","date":"05 Oct 2003","slug":"/2003-10-05-animal-park-overload/"}}}}

